<template lang="pug">
    div.press-content
        ul.pressTimeType
            li.pressTimeTypeItem
                span.item-title(:class="{'active' : activeMainType == 'time'}" @click="mainTabChange('time')") 按时直播
            li.pressTimeTypeItem
                span.item-title(:class="{'active' : activeMainType == 'site'}" @click="mainTabChange('site')") 按场直播

        //- 按时
        template(v-if="activeMainType == 'time'")
            record-time
        //- 按场
        template(v-if="activeMainType == 'site'")
            record-site
</template>
<script>
import recordTime from '@/views/user/record/time'
import recordSite from '@/views/user/record/site'
import { mapGetters, mapActions } from 'vuex'
import { Indicator, Toast } from 'mint-ui'
export default {
    name: 'recordIndex',
    components: {
        recordTime,
        recordSite
    },
    beforeRouteEnter(to, from, next) {   // 路由进来之前
        next(vm => {
            vm.setFooterBarActiveName('userRecord');
            $(".page-content").scrollTop(0);
        });
    },
    data() {
        return {
            activeMainType: 'time'
        }
    },
    methods: {
        ...mapActions(['setFooterBarActiveName']),
        //主标题切换
        mainTabChange(name) {
            this.activeMainType = name;
        }
    }
}
</script>
<style lang="less" scoped>
@import '../../../assets/css/variable.less';
.press-content {
    padding-top: 4.6rem;
    .pressTimeType {
        display: flex;
        justify-content: space-around;
        height: 2.15rem;
        line-height: 2.15rem;
        border-bottom: 1px solid #f4f4f4;
        position: fixed;
        background: #fff;
        width: 100%;
        top: 0;
        z-index: 999;
        padding: 0 .5rem;
        .pressTimeTypeItem {
            font-size: .9rem;
            span {
                padding: 0 .3rem .4rem .3rem;
                color: @fc-main;
            }
            .active {
                border-bottom: 2px solid @fc-main;
                color: @fc-main;
                padding: 0 .3rem .4rem .3rem;
            }
        }
    }
}
</style>







