<template lang="pug">
    div.list-block.media-list
        template(v-if="isLoaded")
            template(v-if="list && list.length > 0")
                ul
                    li.record-list(v-for="(item,index) in list")
                        div.item-content
                            div.item-media
                                router-link(:to="{ path : '/user/others', jquery : {to_user_id : item.user_id}}")
                                    img.avator(v-lazy="item.head_image")
                            div.item-inner
                                div.item-title-row
                                    span.item-title(v-text="item.nick_name")
                                    div.praise
                                        i.icon.iconfont.sex-man(v-if="item.sex == 1") &#xe661;
                                        i.icon.iconfont.sex-woman(v-if="item.sex == 2") &#xe662;
                                        img.icon-level(:src="iconLevel[index]")
                                div.item-subtitle(v-text="`贡献的${appInit.diamonds_name}：${item.total_diamonds}`")
                                div.watch(v-text="`开始观看时间：${item.start_time}`")
                            div.item-after(v-text="`观看时长：${item.total_time}钟`")
                    <div v-if="allLoaded" class="no-more-data">无更多数据</div>
            template(v-else)
                null-data
</template>
<script>
import nullData from '@/components/nullData'
import { mapGetters } from 'vuex'
export default {
    props: ['list', 'isLoaded', 'allLoaded'],
    components: {
        nullData,
    },
    computed: {
        ...mapGetters({
            appInit: 'getAppInit'
        }),

        iconLevel() {
            let arr_user_level = [];
            for (let val of this.list) {
                arr_user_level.push(require(`@/assets/img/rank/rank_${val.user_level}.png`));
            }
            return arr_user_level;
        }
    }
}
</script>

<style lang="less" scoped>
@import '../assets/css/variable.less';
.list-block {
    margin-top: 0;
    .record-list {
        border-bottom: 1px solid #f5f5f5;
        .item-content {
            padding: 0 .5rem;
            .item-media {
                .avator {
                    height: 2.15rem;
                    width: 2.15rem;
                    border-radius: 50%;
                    border: 2px solid @fc-main;
                }
            }
            .item-inner {
                margin-left: .5rem;
                padding-right: 0;
                .item-title-row {
                    justify-content: baseline;
                    .item-title {
                        color: @fc-main;
                        font-size: .7rem;
                    }
                    .praise {
                        display: flex;
                        align-items: center;
                        .iconfont {
                            font-size: .75rem;
                            margin: 0 .25rem;
                        }
                        .sex-woman {
                            color: #ff71bb;
                        }
                        .sex-man {
                            color: #3fa2ff;
                        }
                        img {
                            width: 1.3rem;
                            height: .64rem;
                        }
                    }
                }
                .item-subtitle {
                    font-size: .65rem;
                }
                .watch {
                    font-size: .6rem;
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                }
            }
            :after {
              display: none;
            }
            .item-after {
                font-size: .6rem;
                color: #646464;
                margin-left: 0;
            }
        }
    }
}
</style>




