<template lang="pug">
    div
        ul.pay_record
            li.payTypeItem
                span.item-title(:class="{'active' : listType == 'consum'}" @click="subTabChange('consum')") 付费记录
            li.payTypeItem
                span.item-title(:class="{'active' : listType == 'income'}" @click="subTabChange('income')")  收费记录
        mt-loadmore.loadmore-rank(
            :top-method="loadTop"
            :bottom-method="loadBottom"
            :bottom-all-loaded="paging[listType].allLoaded"
            :autoFill="autoFill",
            ref="loadmore"
            @top-status-change="handleTopChange"
            @bottom-status-change="handleBottomChange"
        )     
            mint-loadmore-top(:topStatus="topStatus" slot="top")    
            div.recordList(v-if="listType == 'consum'")
                record-list(:list="list.consum" :isLoaded="isLoaded" :allLoaded="paging[listType].allLoaded")
            div.recordList(v-if="listType == 'income'")
                record-list(:list="list.income" :isLoaded="isLoaded" :allLoaded="paging[listType].allLoaded")
            mint-loadmore-bottom(:bottomStatus="bottomStatus" slot="bottom" v-show="!paging[listType].allLoaded")    
</template>
<script>
import recordList from '@/components/recordList'
import mintLoadmoreTop from '@/components/mintLoadmoreTop'
import mintLoadmoreBottom from '@/components/mintLoadmoreBottom'
import { Indicator, Toast } from 'mint-ui'
export default {
    name: 'recordSite',
    components: {
        recordList,
        mintLoadmoreTop,
        mintLoadmoreBottom
    },
    data() {
        return {
            listWatch: 0,
            paging: {
                consum: {
                    page: 2,
                    has_next: '',
                    allLoaded: false
                },
                income: {
                    page: 2,
                    has_next: '',
                    allLoaded: false
                }
            },
            list: {
                consum: '',
                income: ''
            },
            autoFill: false,
            isLoaded: false,
            allLoaded: false,
            listType: 'consum',
            topStatus: '',
            bottomStatus: ''
        }
    },
    created() {
        this.getData();
    },
    methods: {
        getData(isRefresh = false) {
            let self = this;
            let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/user_par_cont' : self.api.userParCont();
            self.axios.all([self.axios.get(axiosUrl + '&live_pay_type=1&type=0'), self.axios.get(axiosUrl + '&live_pay_type=1&type=1')])
                .then(self.axios.spread(function(consum, income) {
                    self.listWatch++;
                    self.isLoaded = true;

                    self.list.consum = consum.data.data;
                    self.list.income = income.data.data;

                    self.paging.consum = {
                        page: 2,
                        has_next: consum.data.page.has_next,
                        allLoaded: consum.data.page.has_next ? false : true
                    };
                    self.paging.income = {
                        page: 2,
                        has_next: income.data.page.has_next,
                        allLoaded: income.data.page.has_next ? false : true
                    };

                    self.$nextTick(function() {
                        isRefresh && self.$refs.loadmore.onTopLoaded();
                    });
                }))
                .catch(err => {
                    self.listWatch++;
                    Toast(err || '网络异常，请求失败');
                    isRefresh && self.$refs.loadmore.onTopLoaded();
                })
        },
        subTabChange(listType) {
            this.listType = listType;
        },
        loadTop() {
            let self = this;
            let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/user_par_cont' : self.api.userParCont();
            self.axios.get(axiosUrl, {
                params: {
                    live_pay_type: 1,
                    type: self.listType == 'consum' ? 0 : 1
                }
            })
                .then(res => {
                    self.listWatch++;
                    if (res.data.status == 1) {
                        self.list[self.listType] = res.data.data;
                        self.paging[self.listType].page = 2;
                        self.paging[self.listType].has_next = res.data.page.has_next;
                        self.paging[self.listType].allLoaded = res.data.page.has_next ? false : true;
                    }
                    else {
                        Toast(res.data.error || '请求出错');
                    }

                    self.$nextTick(function() {
                        self.$refs.loadmore.onTopLoaded();
                    });
                })
                .catch(err => {
                    self.listWatch++;
                    Toast(err || '网络异常，请求失败');
                    self.$refs.loadmore.onTopLoaded();
                });
        },
        loadBottom() {
            // 无限加载
            let self = this;

            if (self.paging[self.listType].has_next) {
                let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/user_par_cont' : self.api.userParCont();
                self.axios.get(axiosUrl, {
                    params: {
                        live_pay_type: 1,
                        type: self.listType == 'consum' ? 0 : 1
                    }
                })
                    .then(res => {

                        setTimeout(() => {
                            if (res.data.status == 1) {

                                self.paging[self.listType].has_next = res.data.page.has_next;
                                self.paging[this.listType].allLoaded = res.data.page.has_next ? false : true;
                                self.paging[self.listType].page = self.paging[self.listType].page + 1;

                                if (res.data.data && res.data.data.length && res.data.data.length > 0) {
                                    for (let i = 0; i < res.data.data.length; i++) {
                                        self.list[self.listType].push(res.data.data[i]);
                                    }
                                }

                            }
                            else {
                                Toast(res.data.error || '操作失败');
                            }
                            self.$nextTick(function() {
                                // self.init();
                                self.$refs.loadmore.onBottomLoaded();
                            });
                        }, 500);
                    })
                    .catch(err => {
                        Toast(err || '网络异常');
                        self.$refs.loadmore.onBottomLoaded();
                    });
            }
            else {
                return false;
            }
        },
        // init() {
        //     let self = this,
        //         liveContainer_h = $(".swiper-slide-active").find(".data-container").height(),
        //         screen_h = screen.height;
        //     self.swiperContainerHeight = liveContainer_h > (screen_h - 178) ? liveContainer_h : (screen_h - 178);

        //     self.swiperOption.initialSlide = 1;
        //     self.swiperOption.onSlideChangeStart = (swiper) => {
        //         window.scroll(0, 0);
        //         self.realIndex = swiper.realIndex;
        //         self.listType = self.listCategory[swiper.realIndex].type;


        //         let liveContainer_h = $(".swiper-slide-active").find(".data-container").height(),
        //             screen_h = screen.height;
        //         self.swiperContainerHeight = liveContainer_h > (screen_h - 178) ? liveContainer_h : (screen_h - 178);
        //     };
        // },
        handleTopChange(status) {
            // 获取下拉最新状态
            this.topStatus = status;
        },
        handleBottomChange(status) {
            // 获取上拉最新状态
            this.bottomStatus = status;
        }
    },
    watch: {
        // listWatch() {
        //     // 当页面数据发生改变,进行处理
        //     this.paging[this.listType].allLoaded = !Boolean(this.paging[this.listType].has_next);

        // }
    }
}
</script>
<style lang="less" scoped>
@import '../../../assets/css/variable.less';
.pay_record {
    display: flex;
    justify-content: space-around;
    height: 2.5rem;
    line-height: 2.5rem;
    border-bottom: 1px solid #f4f4f4;
    position: fixed;
    background: #fff;
    width: 100%;
    top: 2.15rem;
    z-index: 999;
    .payTypeItem {
        font-size: .75rem;
        span {
            color: @fc-main;
            padding: 0 1rem .7rem 1rem;
        }
        .active {
            border-bottom: 2px solid @fc-main;
            color: @fc-main;
            padding: 0 1rem .7rem 1rem;
        }
    }
}

</style>



